/**axios封装
 * 请求拦截、相应拦截、错误统一处理
 */
import router from '../router/index'
import http from "./ajax"
import VueCookies from 'vue-cookies'
import axios from 'axios';
import { Message } from 'element-ui';
// import store from '../store/index'
// 环境的切换
// let rootsd = process.env.NODE_ENV;
// switch (rootsd) {
//     case "development": //开发环境
//         axios.defaults.baseURL = 'http://xyj.grapefruitcloud.com';
//         break;
//     case "production": //生产环境
//         axios.defaults.baseURL = 'http://xyj.grapefruitcloud.com';
//         break;
//     default: //测试环境
//         axios.defaults.baseURL = 'http://39.100.131.247';
// }
var href = window.location.href

if (href.indexOf('xyj.grapefruitcloud.com') >= 0) {
    var userUrl = 'http://xyj.grapefruitcloud.com' //PHP服务URL 
} else {
    var userUrl = 'http://39.100.131.247'
    var yourUrl = 'http://test.zzb.com'
        // var yourUrl = 'http://10.0.0.140:8090'
}
// 请求超时时间
axios.defaults.timeout = 10000;
// post请求头
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

// 请求拦截器
axios.interceptors.request.use(
        config => {
            var checkwithout = [
                userUrl + "/clouduser/loginbyphone",
                userUrl + "/clouduser/login",
                userUrl + "/clouduser/register",
                userUrl + "/clouduser/getcode",
                userUrl + "/clouduser/getemail",
                userUrl + "/clouduser/checktoken",
                userUrl + "/clouduser/forgetpassword",
                userUrl + "/clouduser/resetpassword"
            ];
            if (checkwithout.indexOf(config.url) < 0) {
                http.request({
                    "url": userUrl + "/clouduser/checktoken",
                    "method": "post",
                    "data": { "id": VueCookies.get("id"), "token": VueCookies.get("token") },
                    async: true,
                    success: function(data) {
                        data = JSON.parse(data)
                        if (data.status != 0) {
                            VueCookies.set("id", "", 0);
                            VueCookies.set("token", "", 0);
                            VueCookies.set("user", "", 0);
                            alert("该用户已在其他地方登陆")
                            router.push("/")
                        }
                    }
                })
            }
            return config;
            // 每次发送请求之前判断是否存在token，如果存在，则统一在http请求的header都加上token，不用每次请求都手动添加了
            // 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行判断
            // const token = store.state.token;
            // token && (config.headers.Authorization = token);

        },
        error => {
            return Promise.error(error);
        })
    // 响应拦截器
axios.interceptors.response.use(
    response => {

        //console.log(response.status)
        if (response.status === 200) {
            //console.log(Promise.resolve(response));
            if (response.data.status == -5) {
                Message.error('服务器响应超时!');
            }
            return Promise.resolve(response);
        } else {
            return Promise.reject(response);
        }
    },
    // 服务器状态码不是200的情况
    error => {
        //console.log(error.response.status);
        if (error.response.status) {
            switch (error.response.status) {
                // 401: 未登录
                // 未登录则跳转登录页面，并携带当前页面的路径
                // 在登录成功后返回当前页面，这一步需要在登录页操作。
                case 400:
                    Message.error('服务器无法正常响应!');
                    break;
                    // 403 token过期
                    // 登录过期对用户进行提
                    // 清除本地token和清空vuex中token对象
                    // 跳转登录页面
                case 404:
                    Message.error('请求内容不存在!');
                    break;
                    // 404请求不存在
                case 500:
                    Message.error('服务器内部错误!');
                    break;
                    // 其他错误，直接抛出错误提示
                default:
                    this.$message(
                        error.response.data.message

                    );
            }
            return Promise.reject(error.response);
        }
    }
);
/**
 * get方法，对应get请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function get(url, params) {
    return new Promise((resolve, reject) => {
        axios.get(url, {
                params: params
            })
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                // console.log(err.data);
                reject(err.data)
            })
    });
}
/**
 * post方法，对应post请求
 * @param {String} url [请求的url地址]
 * @param {Object} params [请求时携带的参数]
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err.data)
            })
    });
}


